<template>

    <div
        class="icon"
        :style="{
            width: background ? size + 'rem' : '',
            height: background ? size + 'rem' : '',
            backgroundColor: background ? background : '',
            borderRadius: rounded ? size / 2 + 'rem' : '0'
        }"
    >

    <svg :style="{width: sizeValue, height: sizeValue, fill: color}">
        <use :xlink:href="'#' + icon"/>
    </svg>

    </div>
    
</template>

<script>

    import styles from '../../styles/variables'

    export default {
        props: {
            icon: {required: true},
            size: {default: 'md'},
            color: {default: 'black'},
            background: {default: ''},
            rounded: {default: true}
        },

        computed: {
            sizeValue: function() {
                return styles.iconSizes[this.size]
            },
            iconSize: function() {
                return this.background ? this.size * 0.6 : this.size
            }
        },
        mounted() {
            require('../../svg/' + this.icon + '.svg')
        }
    }

</script>
<style>
    .icon {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        border-radius: 2rem;
    }
</style>


